
import React from "react";

import { Layout, Menu } from "antd";
import {
  ReadOutlined,
  UserOutlined,
  TagOutlined,
  FileTextOutlined,
  BarsOutlined,
} from "@ant-design/icons";
import SwitchablePicker from "./components/SelectDate";
import ArticleList from "./components/ArticleList";
import "./App.css";

const { Content, Sider } = Layout;




class BisonSearch extends React.Component {

  constructor(props) {
    super(props);

    this.initialState = {
      collapsed: false,
      selectedOption: "keyword",
      beginDate: '0000',
      finalDate: '9999'
    };

    this.state = this.initialState;

  }

  handleClearDate = (e) => {
    console.log('date cleared');
    this.setState({ beginDate: '0000' });
    this.setState({ finalDate: '9999' });
  }

  handleBeginDate = (e) => {
    console.log(e);
    console.log('in begin date ' + e);
    this.setState({ beginDate: e });
    console.log(this.state.beginDate);
  }

  handleFinalDate = (e) => {
    console.log(e);
    console.log('in end date ' + e);
    this.setState({ finalDate: e });
    console.log(this.state.finalDate);
  }

  handleChange = (e) => {
    var selectedOption = e.key;
    this.setState({ selectedOption });
    console.log(e.key);
  };

  onCollapse = (collapsed) => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  render() {
    return (
      <Layout>
        <Sider breakpoint="md" collapsedWidth="0">

          <h1>Category Search</h1>
          <Menu data-id='searchTypeMenu' theme="dark" mode="inline" defaultSelectedKeys={["keyword"]}>
            <Menu.Item onClick={this.handleChange} key="keyword" icon={<BarsOutlined />}>
              Search by Keyword
            </Menu.Item>
            <Menu.Item onClick={this.handleChange} key="author" icon={<UserOutlined />}>
              Search by Author
            </Menu.Item>
            <Menu.Item onClick={this.handleChange} key="article" icon={<ReadOutlined />}>
              Search by Title
            </Menu.Item>
            <Menu.Item onClick={this.handleChange} key="tag" icon={<TagOutlined />}>
              Search by Tag
            </Menu.Item>
            <Menu.Item onClick={this.handleChange} key="text" icon={<FileTextOutlined />}>
              Search by Text
            </Menu.Item>
          </Menu>

          <h1>Date Limiter</h1>
          <div className="DateSearchDiv">
            <SwitchablePicker
              onSDateChange={this.handleBeginDate}
              onEDateChange={this.handleFinalDate}
              onClearChange={this.handleClearDate}
            />
          </div>
        </Sider>
        <Layout>
          <Content style={{ margin: "24px 16px 0" }}>
            <ArticleList
              mysearch={this.state.selectedOption}
              query="bison"
              bDate={this.state.beginDate}
              eDate={this.state.finalDate}
            >
            </ArticleList>
          </Content>
        </Layout>
      </Layout>
    );
  }
}

function App() {
  return (
    <div className="App">
      <BisonSearch></BisonSearch>
    </div>
  );
}

export default App;
