import React from 'react';
import axios from 'axios';
import { Pagination, Input, Card, Tooltip, Button, Tag, Row, Col } from 'antd';
import { ArrowRightOutlined } from '@ant-design/icons';
import 'antd/dist/antd.css';


const { Search } = Input;


// API for Articles with 3 lvl field *.*.*
const token = 'LD4e9CjPc5G01j25lzQO';
const apiSearch = 'https://digital.harding.edu/BisonsUI/API/';

var type = "article";
var beginDate = '0000';
var finalDate = '9999';


function getApiQuery(query) {
  console.log("type: " + type)

  var search = apiSearch;
  search += '?type=' + type;
  search += '&query=' + query;
  search += '&bdate=' + beginDate;
  search += '&edate=' + finalDate;

  return search;
}

function getArticleURL(item) {
  return (
    "https://digital.harding.edu/thebison/" +
    item.release_date +
    ".pdf#page=" +
    item.page
  );
}

function authorValidation(article) {

  if (article.author === undefined || article.author.length === 0) {

    return 'unknown';

  } else {
    var full_name = article.author[0].author_id.full_name;
    return full_name;

  }

}


function get_temp_list() {
  const temp_articles = [1, 2, 3, 4, 5]

  return (
    temp_articles.map(article =>
      <Card
        loading={true}
        key={'temp article' + article}
      >
      </Card>
    )
  )
}


function get_list(articles, query, loading) {
  return (
    articles.map(article =>
      <Card
        loading={loading}
        key={query + article.title}
      >

        <Row className="cardTopPart">
          <Col span={21}>
            <h3>{article.title}</h3>
          </Col>
          <Col span={3}>

            <Tooltip title="Read the article">
              <Button
                type="circle link"
                target="_blank"
                href={getArticleURL(article)}
                icon={<ArrowRightOutlined />}
              ></Button>
            </Tooltip>
          </Col>


        </Row>

        <p className="card-date">
          In: {article.release_date}, page: {article.page}
        </p>

        <p className="card-author">
          By: {authorValidation(article)}
        </p>

        <div className="card-tags">
          {article.tag.map(x => (
            <Tag key={x.tag_id.subject}>{x.tag_id.subject}</Tag>
          ))}
        </div>
      </Card>
    )
  )
}

function not_found(query) {
  console.log(query);
  return (
    < Card
      key={query + 'not_found'
      }
    >
      <h3>{'Your search: "' + query + '" found no results.'}</h3>
    </Card >
  )
}

function get_search_result_num(num){
  return (
    <p className="results-num">Your search found {num} results.</p>
  );
}

export default class ArticleList extends React.Component {

  state = {
    articles: [],
    found: false,
    current_search: 'bison',
    loading: true,
    is_temp: true,
    cur_page: 1,
    page_size: 10,
    cur_articles: null,
    last_key: 0,
  }


  onSearch = value => (
    this.updateArticleList(value)
  );

  componentDidMount() {
    this.setState({ loading: true })
    axios.get(getApiQuery(this.props.query), { headers: { 'Authorization': `Bearer ${token}` } })
      .then(res => {
        const found = true;
        const articles = Array.from(res.data.data);
        this.setState({ loading: true, found, articles, is_temp: false });
      })
  }

  updateArticleList(value) {
    this.cur_page = 0;
    this.setState({ loading: true }, () =>
      axios.get(getApiQuery(value), { headers: { 'Authorization': `Bearer ${token}` } })
        .then(res => {
          const articles = Array.from(res.data.data);

          console.log(res.data.data.length)
          console.log(articles.length)
          if ((typeof res.data.data !== 'undefined') && (articles.length !== 0)) {
            const found = true;
            const current_search = value;
            this.setState({loading: false, found, articles, current_search });
          } else {
            console.log('no results')
            const found = false;
            const current_search = value;
            this.setState({ loading: false, found, current_search });
          }
        })
    )
  }

  showSearchResults(search_state) {


    var pagination_change = page => {
      var new_key = this.state.last_key + 1;

      this.setState({cur_page: page});
      this.setState( {last_key: new_key});
      this.setState({ loading: false, found: search_state.found, current_search: search_state.query });
    }

    var page_size_change = (page, size) => {
      console.log(size);
      //this.state.page_size = size;
      this.setState({page_size: size});
      console.log('Page size: ' + this.state.page_size);
    }



    if (search_state.is_temp) {
      return get_temp_list()
    } else if (!search_state.found) {
      return not_found(search_state.query);
    } else {

      const pageSizeOptions = [10,20,50];
      var begin_page = (this.state.cur_page - 1) * 10;
      var final_page = begin_page + this.state.page_size;
      var total = search_state.articles.length;

      //var current_articles = search_state.articles.slice(begin_page, final_page);
     // this.setState({cur_articles: current_articles })
      this.state.cur_articles = search_state.articles.slice(begin_page, final_page);

      //console.log(current_articles);
      //console.log(this.state.cur_articles);

      console.log('Found Articles: ' + total)
      if (this.state.loading){
        return (
          <>
            {get_list(this.state.cur_articles, search_state.query, search_state.is_loading)}
          </>
        );
      } else {
        return (
          <>
            {get_search_result_num(total)}
            <Pagination
              size="small"
              current={this.state.cur_page}
              total={total}
              onChange={pagination_change}
              onShowSizeChange={page_size_change}
              pageSizeOptions={pageSizeOptions}
              showQuickJumper
            />
            {get_list(this.state.cur_articles, search_state.query, search_state.is_loading)}
            <Pagination
              size="small"
              current={this.state.cur_page}
              total={total}
              onChange={pagination_change}
              onShowSizeChange={page_size_change}
              pageSizeOptions={pageSizeOptions}
              showQuickJumper
            />
          </>
        );
      }

    }

  }

  performSearch(search_state){
    if (search_state.query == "keyword"){
      console.log("we are inside keyword")
    }

    return this.showSearchResults(search_state);
  }

  render() {
    const { loading } = this.state;
    type = this.props.mysearch;
    beginDate = this.props.bDate;
    finalDate = this.props.eDate;

    var search_state = {
      found: this.state.found,
      articles: this.state.articles,
      query: this.state.current_search,
      is_loading: this.state.loading,
      is_temp: this.state.is_temp
    }

    return (

      <Row gutter={[8, 24]} justify="center">
        <Col>
          <div id="SearchBox">
            <div key={"The Search Box" + this.last_key} id='BisonsList'>
              <Search placeholder="Search an article" onSearch={this.onSearch} enterButton />
              {this.performSearch(search_state)}
            </div>

          </div>
        </Col>
      </Row>


    )
  }

}