import React, { useState } from "react";
import "antd/dist/antd.css";
import { DatePicker, Select, Space, Row, Col, Button } from "antd";

const { Option } = Select;

function PickerWithType({ type, onChange }) {
  console.log(type);

  if (type === "date") return <DatePicker onChange={onChange} />;
  return <DatePicker picker={type} onChange={onChange} />;
}

function SDateFormatter(moment, type) {
  if (moment == null) {
    return "0000";
  }

  if (type === "date") {
    return moment.format("YYYY-MM-DD");
  } else if (type === "month") {
    return moment.format("YYYY-MM");
  } else {
    return moment.format("YYYY");
  }
}

function EDateFormatter(moment, type) {
  if (moment == null) {
    return "9999";
  }

  if (type === "date") {
    return moment.format("YYYY-MM-DD");
  } else if (type === "month") {
    return moment.format("YYYY-MM");
  } else {
    return moment.format("YYYY");
  }
}

function SwitchablePicker(props) {
  const [type, setType] = useState("year");
  const [picker, setPicker] = useState(0);
  const [term, setTerm] = useState(0);

  function clearer() {
    setPicker(picker + 1);
    setTerm(0);
    props.onClearChange();
  }

  return (
    <div key={picker}>
      <Space size="small">
        <h4>Filter By:</h4>
        <Select
          className="dateSearchSelect"
          value={type}
          bordered={false}
          onChange={setType}
        >
          <Option value="date">Date</Option>
          <Option value="month">Month</Option>
          <Option value="year">Year</Option>
        </Select>
      </Space>

      <Row gutter={[16, 8]} className="datePickerSearch">
        <Col span={5}>
          <h4>From: </h4>
        </Col>
        <Col span={19}>
          <PickerWithType
            type={type}
            onChange={(value) => (
              props.onSDateChange(SDateFormatter(value, type)),
              setTerm(term + 1)
            )}
          />
        </Col>
      </Row>

      <Row gutter={[16, 8]} className="datePickerSearch">
        <Col span={5}>
          <h4>To: </h4>
        </Col>
        <Col span={19}>
          <PickerWithType
            type={type}
            onChange={(value) => (
              props.onEDateChange(EDateFormatter(value, type)),
              setTerm(term + 1)
            )
            }
          />
        </Col>
      </Row>
      {term !== 0 ? (
        <>
          <span>
            <Button
              type="link"
              className="clear-button"
              onClick={() => clearer()}
            >
              Clear
            </Button>
          </span>
        </>
      ) : (
        <></>
      )}
    </div>
  );
}

export default SwitchablePicker;
